<template>
  <div class="index">
    <el-main class="mainleft">
    	<p class="indextitle">首页</p>
    	<ul>
    		<h1>平台运营情况</h1>
    		<li>当前入住学校数量0所</li>
    		<li>学生数量0人</li>
    		<li>账户数量0个</li>
    		<li>缴费到期学校数量0所</li>
    		<li>广告数量0所</li>
    		<li>发布的咨询数量0所</li>
    	</ul>
    </el-main>
    <el-main class="mainright">
    	<div class="echartscoat">
			<h1>系统运营情况</h1>
    		<div id="myChart" :style="{width: '100%', height: '360px'}"></div>
    	</div>
    </el-main>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
		sesname:""	
    }
  },
  methods:{
	  drawLine () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById('myChart'))
      // 绘制图表
      myChart.setOption({
		 color: ['#1FD5B0'],
        tooltip: {},
		 legend: {
        data:['present']},
        xAxis: {
        type: 'category',
        data: ['空闲率',  '使用率'],
		 axisTick: {
            show: false
        },
        axisLine: {
            show: false
        },
        z: 10
    },
    yAxis: {
        type: 'value',
		 axisTick: {
		    show: false
		},
		axisLine: {
		    show: false
		},
		z: 10
    },
    series: [{
		name:'present',
        data: [90,10],
        type: 'line'
    }]
      })
	}
  },
  mounted:function() {
    this.drawLine();
  },
  created:function(){
  	   this.sesname = window.sessionStorage.getItem("username");
  	  if(!this.sesname){
  	     this.$router.push("/index");
  	  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mainleft{
	  height:400px;
		float:left;
  }
  .indextitle{
	  height:45px;
	  text-align: left;
  }
  .mainleft ul{
	  width: 445px;
	  height:300px;
	  box-shadow: 0px 0px 5px darkgray;
  }
  .mainleft ul h1{
	  height:55px;
	  line-height:55px;
	  border-bottom:solid 1px #EFF1F7;
	  text-indent: 20px;
	  margin-bottom:10px;
  }
  .mainleft ul li{
	  height:35px;
	  line-height:35px;
	  width:405px;
	  margin:0 auto;
	  border-bottom:solid 1px #EFF1F7;
	  font-size:15px;
  }
  .mainright{
  	  height:520px;
  }
  .mainright .echartscoat{
	  margin-top:45px;
	  height:420px;
	  box-shadow: 0px 0px 5px darkgray;
  }
  .mainright div h1{
  	  height:55px;
  	  line-height:55px;
  	  border-bottom:solid 1px #EFF1F7;
  	  text-indent: 20px;
  }
</style>


