<template>
  <div class="teacher">
    <!-- 头部 -->
    <el-breadcrumb separator="/">
    		<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
    		<el-breadcrumb-item><a href="/">用户管理</a></el-breadcrumb-item>
    		<el-breadcrumb-item>教师列表</el-breadcrumb-item>
    </el-breadcrumb>	
    <!-- 搜索 -->
    <div class="teacherhead">
    	<div class="teachercoat">
    		<el-select v-model="value" clearable placeholder="请选择学校">
    	<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
    	</el-option>
    		</el-select>
    			<el-input class="teacherinput" size="medium" placeholder="请输入教师姓名"  clearable></el-input>
    		<el-button type="primary">搜索</el-button>
    	</div>
    </div>
    <!-- 表格 -->
    <el-table :data="tableData" border style="width: 100%;" :default-sort = "{prop: 'id', order: 'descending'}">
    <el-table-column prop="id" label="序号"  width="50"></el-table-column>
    <el-table-column prop="name" label="姓名" width="145" ></el-table-column>	
    <el-table-column prop="number" label="编号"  width="145"></el-table-column>
    <el-table-column prop="sex" label="性别" width="145"></el-table-column>
    <el-table-column prop="teachingtime" label="教学时间"  width="145"></el-table-column>
    <el-table-column prop="school" label="所属学校" width="145" ></el-table-column>	
    <el-table-column prop="title" label="职称"  width="145"></el-table-column>
    <el-table-column prop="phone" label="电话" width="145"></el-table-column>
    </el-table>
    
    <!-- 分页 -->
    <div class="teacherfoot">
    	<div class="block">
    		<el-pagination @size-change="handleSizeChange"
    			@current-change="handleCurrentChange"
    			:current-page="currentPage4"
    			:page-sizes="[10, 20, 30]"
    			:page-size="10"
    			layout="total, sizes, prev, pager, next, jumper"
    			:total="3">
    		</el-pagination>
    	</div>
    </div>	
  </div>
</template>

<script>
export default {
  name: 'Teacher',
  data () {
    return {
		tableData:[],
		options: [{
		    value: '选项1',
		    label: '黄金糕'
		  }, {
		    value: '选项2',
		    label: '双皮奶'
		  }, {
		    value: '选项3',
		    label: '蚵仔煎'
		  }, {
		    value: '选项4',
		    label: '龙须面'
		  }, {
		    value: '选项5',
		    label: '北京烤鸭'
		  }],
		  value: '',
		 currentPage4: 1,
		 sesname:''
    }
  },
  methods:{
	  handleSizeChange(val) {
	     console.log(`每页 ${val} 条`);
	   },
	   handleCurrentChange(val) {
	     console.log(`当前页: ${val}`);
	   },
  },
  created:function(){
  	  this.$http.get("../../static/teacherlist.json").then(
  			function(res){
				console.log(res.data);
  				this.tableData=res.data;
  			},
  			function(err){
  				console.log(err);
  			}
  	  ),
	  this.sesname = window.sessionStorage.getItem("username");
	  if(!this.sesname){
	     this.$router.push("/index");
	  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.teacherhead{
		background:#F2F2F2;
		height:65px;
		margin-top:20px;
	}
	.teacherinput{
		width:150px;
		height:30px;
	}
	.teachercoat{
		height:30px;
		line-height: 65px;
		margin-left:20px;
	}
	.el-table{
		margin-top:30px;
		border-top:0;
	}
</style>



