<template>
  <div class="ads">
    
    <!-- 头部 -->
    <el-breadcrumb separator="/">
    		<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
    		<el-breadcrumb-item><a href="/">用户管理</a></el-breadcrumb-item>
    		<el-breadcrumb-item>广告列表</el-breadcrumb-item>
    </el-breadcrumb>	
    <!-- 搜索 -->
    <div class="adshead">
    	<div class="adscoat">
    			<el-input class="adsinput" size="medium" placeholder="请输入广告名称"  clearable></el-input>
    		<el-button type="primary">搜索</el-button>
    		<el-button type="primary"  @click="addschool('ruleForm')">添加广告</el-button>
    	</div>
    </div>
    <!-- 表格 -->
    <el-table :data="tableData" border style="width: 100%;"  @current-change="idHandleCurrentChange">
    <el-table-column type="index" label="序号"  width="65"></el-table-column>
    <el-table-column prop="name" label="名称" width="100" ></el-table-column>	
    <el-table-column prop="date" label="封面图"  width="100">
				 <!--插入图片链接的代码-->
			<template slot-scope="scope">
			  <img  src="../../static/monkey.jpg" alt="" style="width: 50px;height: 50px">
			</template>
		</el-table-column>
    <el-table-column prop="href" label="外链" width="100"></el-table-column>
    <el-table-column prop="readnum" label="阅读量"  width="100"></el-table-column>
    <el-table-column prop="operationuser" label="操作人"  width="100"></el-table-column>
    <el-table-column prop="" label="操作账号" width="100"></el-table-column>
    <el-table-column prop="state" label="状态" width="100"></el-table-column>
    <el-table-column prop="date" label="操作">
				<template slot-scope="scope">
					<el-button size="mini" type="info" @click="findschool(scope.$index)">查看</el-button>
					<el-button size="mini" @click="editschool(scope.$index)">编辑</el-button>
					<el-button size="mini" type="danger" @click="delschool(scope.$index)">删除</el-button>	
				</template>
		</el-table-column>
    </el-table>
    
    <!-- 分页 -->
    <div class="adsfoot">
    	<div class="block">
    		<el-pagination @size-change="handleSizeChange"
    			@current-change="handleCurrentChange"
    			:current-page="currentPage4"
    			:page-sizes="[10, 20, 30]"
    			:page-size="10"
    			layout="total, sizes, prev, pager, next, jumper"
    			:total="3">
    		</el-pagination>
    	</div>
    </div>	
    <!-- 查看模态框 -->
    <div class="find"  v-show="findstate">
    	<h5><p>查看</p><i class="el-icon-close" @click="closefind"></i></h5>
    		<ul >
    			<li><label>名称：</label>{{schoolmsg.name}}</li>
    			<li><label>外链：</label>{{schoolmsg.href}}</li>
    			<li><label>阅读量：</label>{{schoolmsg.readnum}}</li>
    			<li><label>操作人：</label>{{schoolmsg.operationuser}}</li>
    			<li><label>状态：</label>{{schoolmsg.state}}</li>
    			<li><label style="float:left;">封面图：</label>
    				<img src="../../static/vue.png" />
    			</li>
    			<div class="schoolfindbtn">
    				<el-button type="primary">停用广告</el-button>
    				<el-button type="danger" @click="deleteschool">删除广告</el-button>
    			</div>
    		</ul>
    </div>	
    
    
    <!-- 模态框背景 -->
    <div class="newsbg" v-show="bgstate"></div>
    <!-- 模态框 -->
    <div class="newsaddcoat" v-show="addstate">
    	<p class="newsaddhead"><label>新增/编辑</label><i class="el-icon-close" @click="canceledit"></i></p>
    	<el-form  :model="schoolmsg" :rules="rules" ref="ruleForm" label-width="110px" class="demo-ruleForm">
    		<el-form-item label="标题" prop="name" required>
    			<el-input v-model="schoolmsg.name"></el-input>
    		</el-form-item>
    		
    		<!-- 封面 -->
    		<el-form-item label="封面">
    			<el-upload
    			  class="avatar-uploader123"
    			  action="https://jsonplaceholder.typicode.com/posts/"
    			  :show-file-list="false"
    			  :on-success="handleAvatarSuccess"
    			  :before-upload="beforeAvatarUpload">
    			  <img v-if="imageUrl" :src="imageUrl" class="avatar">
    			  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    			</el-upload>
    		</el-form-item>
    		
    		<el-form-item label="配图" >
    			<el-upload
    			action="https://jsonplaceholder.typicode.com/posts/"
    			list-type="picture-card"
    			:on-preview="handlePictureCardPreview"
    			:on-remove="handleRemove">
    			<i class="el-icon-plus"></i>
    		</el-upload>
    		<el-dialog :visible.sync="dialogVisible">
    			<img width="100%" :src="dialogImageUrl" alt="">
    		</el-dialog>
    		</el-form-item>
    		
    		
    		
    		<el-form-item label="范围" prop="region">
    			<el-select v-model="schoolmsg.region" placeholder="请选择区域">
    				<el-option label="西安" value="西安"></el-option>
    				<el-option label="西安1" value="西安1"></el-option>
    				<el-option label="西安2" value="西安2"></el-option>
    			</el-select>
    			<el-select v-model="schoolmsg.school" placeholder="请选择学校">
    				<el-option label="猿究院" value="猿究院"></el-option>
    				<el-option label="猿究院1" value="猿究院1"></el-option>
    				<el-option label="猿究院2" value="猿究院2"></el-option>
    			</el-select>
    		</el-form-item>
    		
    		<el-form-item label="正文" prop="desc">
    			<el-input type="textarea" style="width:90%;" v-model="ruleForm.desc"></el-input>
    		</el-form-item>
    		
    		</el-form-item>
    				<div class="newsbtncoat">
    					<el-button type="primary" @click="submitForm('ruleForm')" >确定{{addoredit}}</el-button>
    					<el-button @click="resetForm('ruleForm')">重置</el-button>
    				</div>
    		</el-form>
    </div>
   
  </div>
</template>

<script>
export default {
  name: 'Ad',
  data () {
    return {
			tableData:[],
		options: [{
		    value: '选项1',
		    label: '黄金糕'
		  }, {
		    value: '选项2',
		    label: '双皮奶'
		  }, {
		    value: '选项3',
		    label: '蚵仔煎'
		  }, {
		    value: '选项4',
		    label: '龙须面'
		  }, {
		    value: '选项5',
		    label: '北京烤鸭'
		  }],
			currentPage4: 1	,
		  value: '',
			ruleForm: {
			  name: '',
			  region: '',
			},
			rules: {
			    name: [
			      { required: true, message: '请输入广告名称', trigger: 'change' },
			      { min: 3, max:10, message: '长度在 3 到 10个字符', trigger: 'change' },
						{pattern: /^[\u4E00-\u9FA5]+$/, message: '广告名只能为中文'}
			    ],
			    region: [
			      { required: true, message: '请选择区域', trigger: 'change' }
			    ],
			},
		schoolmsg:{"id":"11","name":"","href":"www.baidu.com","readnum":"100","operationuser":"王岩仪","state":"正常"},
		imageUrl: '',
		dialogImageUrl: '',
		dialogVisible: false,
		tempData:{},
		tempindex:-1,
		/* 查看模态框 */
		findstate:false,
		addstate:false,
		bgstate:false,
		addoredit:"",	
		sesname:''
    }
  },
	methods:{
		/* ID改序号*/
		 idHandleCurrentChange(val) {
		  this.currentRow = val;
		},
		handleSizeChange(val) {
		   console.log(`每页 ${val} 条`);
		 },
		 handleCurrentChange(val) {
		   console.log(`当前页: ${val}`);
		 },
		 handleAvatarSuccess(res, file) {
		   this.imageUrl = URL.createObjectURL(file.raw);
		 },
		 beforeAvatarUpload(file) {
		   const isJPG = file.type === 'image/jpeg';
		   const isLt2M = file.size / 1024 / 1024 < 2;
		
		   if (!isJPG) {
		     this.$message.error('上传头像图片只能是 JPG 格式!');
		   }
		   if (!isLt2M) {
		     this.$message.error('上传头像图片大小不能超过 2MB!');
		   }
		   return isJPG && isLt2M;
		 },
		handleRemove(file, fileList) {
		   console.log(file, fileList);
		 },
		 handlePictureCardPreview(file) {
		   this.dialogImageUrl = file.url;
		   this.dialogVisible = true;
		 },
		  /* 深拷贝*/
		 	    deepCopy:function (obj) {
		 	    var result = Array.isArray(obj) ? [] : {};
		 	    for (var key in obj) {
		 	      if (obj.hasOwnProperty(key)) {
		 	        if (typeof obj[key] === 'object' && obj[key]!==null) {
		 	          result[key] = deepCopy(obj[key]);   //递归复制
		 	        } else {
		 	          result[key] = obj[key];
		 	        }
		 	      }
		 	    }
		 	    return result;
		 	  },
		 	  
		 	     /* 查看信息*/
		 	  findschool:function(index){
		 	  		  this.findstate=true;
		 	  		  this.bgstate=true;
		 	  		  this.tempindex=index;
		 	  		  this.schoolmsg=this.tableData[index];
		 	  },
		 	  closefind:function(){
		 	  		 this.findstate=false;
		 	  		 this.bgstate=false;
		 	  		 this.schoolmsg={"id":"11","name":"","href":"www.baidu.com","readnum":"100","operationuser":"王岩仪","state":"正常"};
		 	  },
		 	 /* 删除信息*/
		 	  deleteschool:function(){
		 	  	this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
		 	        confirmButtonText: '确定',
		 	        cancelButtonText: '取消',
		 	        type: 'warning'
		 	      }).then(() => {
		 	  		 this.tableData.splice(this.tempindex,1);
		 	  		this.findstate=false;
		 	  		this.bgstate=false;
		 	  		this.tempindex=-1;
		 	        this.$message({
		 	  		showClose: true,
		 	          type: 'success',
		 	          message: '删除成功!'
		 	        });
		 	      }).catch(() => {
		 	        this.$message({
		 	  		showClose: true,
		 	          type: 'info',
		 	          message: '已取消删除'
		 	        });          
		 	      });
		 	  },
		 	   /* 删除*/
		 	  delschool:function(index){
		 	  		  this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
		 	      confirmButtonText: '确定',
		 	      cancelButtonText: '取消',
		 	      type: 'warning'
		 	    }).then(() => {
		 	  			this.tableData.splice(index,1);
		 	      this.$message({
		 	  			showClose: true,
		 	        type: 'success',
		 	        message: '删除成功!'
		 	      });
		 	    }).catch(() => {
		 	      this.$message({
		 	  			showClose: true,
		 	        type: 'info',
		 	        message: '已取消删除'
		 	      });          
		 	    });
		 	  },
		 	  /* 编辑*/
		 	  /* 增改信息*/
		 	  editschool:function(index){
		 	  			this.tempData=this.deepCopy(this.tableData[index]);
		 	  			this.tempindex=index;
		 	  			this.addoredit="修改";
		 	  			this.addstate=true;
		 	  			this.bgstate=true;
		 	  			/* 保存数据 */
		 	  			this.schoolmsg = this.tableData[index];
		 	  },
		 	  /* 取消新增或修改*/
		 	  canceledit:function(){
		 	  		  if(this.addoredit=="修改"){
		 	  			  this.tableData.splice(this.tempindex,1,this.tempData);
		 	  			  this.addstate=false;
		 	  			  this.bgstate=false;
		 	  			  this.tempData={};
		 	  			  this.tempindex=-1;
		 	  		  }else if(this.addoredit=="新增"){
		 	  			  this.addstate=false;
		 	  			  this.bgstate=false;
		 	  		  }else{
		 	  			  console.log("404");
		 	  		  }
		 	  },
		 	  
		 	  /* TODO重置后不再非空验证*/
		 	  submitForm(formName) {
		 	    this.$refs[formName].validate((valid) => {
		 	      if(this.addoredit=="修改"){
		 	  			  if (valid) {
		 	  				  this.$confirm('此操作将修改该数据, 是否继续?', '提示', {
		 	  				    confirmButtonText: '确定',
		 	  				    cancelButtonText: '取消',
		 	  				    type: 'warning'
		 	  				  }).then(() => {
		 	  				  	this.addstate=false;
		 	  				  	this.bgstate=false;
		 	  				  	this.schoolmsg={"id":"11","name":"","href":"www.baidu.com","readnum":"100","operationuser":"王岩仪","state":"正常"};
		 	  				    this.$message({
		 	  				  	showClose: true,
		 	  				      type: 'success',
		 	  				      message: '修改成功!'
		 	  				    });
		 	  				  }).catch(() => {
		 	  				    this.$message({
		 	  				  	showClose: true,
		 	  				      type: 'info',
		 	  				      message: '已取消修改'
		 	  				    });          
		 	  				  });
		 	  			  } else {
		 	  			    console.log('error 修改失败!!');
		 	  			    return false;
		 	  			  }
		 	  		  }else if(this.addoredit=="新增"){
		 	  			  if (valid) {
		 	  				  this.$message({
		 	  				 showClose: true,
		 	  				   type: 'success',
		 	  				   message: '新增成功!'
		 	  				 });
		 	  				this.tableData.unshift(this.schoolmsg);
		 	  			  			this.addstate=false;
		 	  			  			this.bgstate=false;
		 	  						this.schoolmsg={"id":"11","name":"","href":"www.baidu.com","readnum":"100","operationuser":"王岩仪","state":"正常"};
		 	  			  } else {
		 	  			    console.log('error 新增失败!!');
		 	  			    return false;
		 	  			  }
		 	  		  }
		 	    });
		 	  },
		 	  resetForm(formName) {
		 	    this.$refs[formName].resetFields();
		 	  },
		 	   /* 新增*/
		 	  addschool:function(formName){
		 	  		  this.$refs[formName].resetFields();
		 	  		  this.addoredit="新增";
		 	  		  this.addstate=true;
		 	  		  this.bgstate=true;
		 	  },
	},
	created:function(){
		  this.$http.get("../../static/ad.json").then(
				function(res){
					this.tableData=res.data;
				},
				function(err){
					console.log(err);
				}
		  ),
			 this.sesname = window.sessionStorage.getItem("username");
			if(!this.sesname){
			   this.$router.push("/index");
			}
	},
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .adshead{
 	background:#F2F2F2;
 	height:65px;
 	margin-top:20px;
 }
 .adsinput{
 	width:150px;
 	height:30px;
 }
 .adscoat{
 	height:30px;
 	line-height: 65px;
 	margin-left:20px;
 }
 .el-table{
 	margin-top:30px;
 	border-top:0;
 }
 
 
 
 .newsbg{
		width:100%;
		height:100%;
		background: rgba(0,0,0,0.2);
		position:fixed;
		top:0;
		left:0;
		z-index:10
	}
	.newsaddcoat{
		width:70%;
		height:100%;
		position:fixed;
		top:0%;
		left:14%;
		z-index:11;
		background:white;
		padding-left:20px;
	}
	.newsaddhead i{
		float:right;
		margin-right:20px;
	}
	.newsaddcoat .el-button{
		float:right;
		margin-right:25px;
	}
	.newsaddcoat .el-input{
		width:90%;
	}
 /* 上传 */
 .avatar-uploader123 {
   border: 1px dashed #d9d9d9;
   width:178px;
   border-radius: 6px;
   cursor: pointer;
   position: relative;
   overflow: hidden;
 }
 .avatar-uploader123:hover {
   border-color: #409EFF;
 }
 .avatar-uploader-icon {
   font-size: 28px;
   color: #8c939d;
   width: 178px;
   height: 178px;
   line-height: 178px;
   text-align: center;
 }
 .avatar {
   width: 178px;
   height: 178px;
   display: block;
 }
 /* 查看模态框 */
 .find{
 	width:40%;
 	height:100%;
 	background:white;
 	position:fixed;
 	left:30%;
 	top:0%;
 	z-index:11;
 	font-size:13px;
 }
 .find label{
 	display:inline-block;
 	width:120px;
 	text-align:right;
 	margin-right:20px;
 	margin-left:20px;
 }
 .find ul{
 	margin-top:20px;
 }
 .find h5{
 	margin-top:5px;
 	text-indent:20px;
 }
 .find h5 i{
 	float:right;
 	margin-right:20px;
 }
 .find li{
 	margin-top:10px;
 }
 .find li .el-button{
 	margin-left:80px;
 }
 .find .schoolfindbtn{
 	margin-left:150px;
 }
</style>



