<template>
  <div class="schoolrec">
			<!-- 头部 -->
			<el-breadcrumb separator="/">
					<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
					<el-breadcrumb-item><a href="/">用户管理</a></el-breadcrumb-item>
					<el-breadcrumb-item>学校介绍审核</el-breadcrumb-item>
			</el-breadcrumb>	
			<!-- 搜索 -->
			<div class="schoolrechead">
				<div class="schoolreccoat">
					<el-select v-model="value" clearable placeholder="请选择学校">
			<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
					</el-option>
					</el-select>
					<el-button type="primary">搜索</el-button>
				</div>
			</div>
			<!-- 表格 -->
			<el-table :data="tableData" border style="width: 100%;" @current-change="handleCurrentChange" >
			<el-table-column type="index" label="序号"  width="55"></el-table-column>
			<el-table-column prop="region" label="区域" width="210" ></el-table-column>	
			<el-table-column prop="schoolname" label="学校名称"  width="250"></el-table-column>
			<el-table-column prop="updatetime" label="更新时间"width="250"></el-table-column>
			<el-table-column label="操作"  >
				
			<template slot-scope="scope">
				<el-button size="mini" type="info" @click="findschool(scope.$index)">查看</el-button>
				<el-button size="mini" @click="editschool(scope.$index)">编辑</el-button>
				<el-button size="mini" type="danger" @click="delschool(scope.$index)">删除</el-button>	
			</template>
				
			</el-table-column>	
			</el-table>
			
			
			
			
			<div class="schoolbg" v-show="bgstate"></div>
				
				
				<!-- 查看模态框 -->
				<div class="find"  v-show="findstate">
					<h5><p>查看</p><i class="el-icon-close" @click="closefind"></i></h5>
						<ul >
							<li><label>学校名称：</label>{{schoolmsg.schoolname}}</li>
							<li><label>所在区域：</label>{{schoolmsg.region}}</li>
							<li><label>更新时间：</label>{{schoolmsg.updatetime}}</li>
							<li><label style="float:left;">学校logo：</label>
								<img src="../../static/vue.png" />
							</li>
							<div class="schoolfindbtn">
								<el-button type="primary">停用学校</el-button>
								<el-button type="danger" @click="deleteschool">删除学校</el-button>
							</div>
						</ul>
				</div>	
			
			<!-- 新增编辑模态框 -->
			<div class="schooladdcoat" v-show="addstate">
				<p><label>编辑</label><i class="el-icon-close" @click="canceledit"></i></p>
				<el-form  :model="schoolmsg" :rules="rules" ref="ruleForm" label-width="110px" class="demo-ruleForm">
					<el-form-item label="学校名称" prop="schoolname" >
						<el-input v-model="schoolmsg.schoolname"></el-input>
					</el-form-item>
					<el-form-item label="所在区域" prop="region" >
						<el-select v-model="schoolmsg.region" placeholder="请选择">
							<el-option label="西安市雁塔区" value="西安市雁塔区"></el-option>
							<el-option label="西安市雁塔区1" value="西安市雁塔区1"></el-option>
							<el-option label="西安市雁塔区2" value="西安市雁塔区2"></el-option>
							<el-option label="西安市雁塔区3" value="西安市雁塔区3"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="更新时间" >
						<el-form-item prop="updatetime">
						<el-date-picker type="date" placeholder="选择日期"  format="yyyy 年 MM 月 dd 日"
			  value-format="yyyy-MM-dd" v-model="schoolmsg.updatetime" style="width:94%;padding-right:20px;"></el-date-picker>
						</el-form-item>
					</el-form-item>
							<div class="schoolbtncoat">
								<el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
								<el-button @click="resetForm('ruleForm')">重置</el-button>
							</div>
					</el-form>
				</div>		
			
  </div>
</template>

<script>
export default {
  name: 'SchoolRec',
  data () {
    return {
		tableData:[],
		options: [{
		    value: '选项1',
		    label: '猿究院'
		  }, {
		    value: '选项2',
		    label: '猿究院111'
		  }, {
		    value: '选项3',
		    label: '猿究院222'
		  }, {
		    value: '选项4',
		    label: '猿究院333'
		  }, {
		    value: '选项5',
		    label: '猿究院444'
		  }],
		  value: '',
		tempData:{},
		tempindex:-1,
		/* 查看模态框 */
		findstate:false,
		addstate:false,
		bgstate:false,
		sesname:'',
		schoolmsg:{"id":"0","region":"","schoolname":"","updatetime":"" },
			 ruleForm: {
			region:'',
		    schoolname: '',
		    updatetime: '',
		  },
		  rules: {
		    schoolname: [
		      { required: true, message: '请输入学校名称', trigger: 'change' },
		      { min: 3, max: 10, message: '长度在 3 到 10个字符', trigger: 'change' },
			  {pattern: /^[\u4E00-\u9FA5]+$/, message: '学校名只能为中文'}
		    ],
		    region: [
		      { required: true, message: '请选择区域', trigger: 'change' }
		    ],
		    updatetime: [
		      { required: true, message: '请选择更新时间', trigger: 'change' }
		    ],
		},
    }
  },
  methods:{
	   /* ID改序号*/
	   handleCurrentChange(val) {
	    this.currentRow = val;
	  },
	   /* 深拷贝*/
	    deepCopy:function (obj) {
	    var result = Array.isArray(obj) ? [] : {};
	    for (var key in obj) {
	      if (obj.hasOwnProperty(key)) {
	        if (typeof obj[key] === 'object' && obj[key]!==null) {
	          result[key] = deepCopy(obj[key]);   //递归复制
	        } else {
	          result[key] = obj[key];
	        }
	      }
	    }
	    return result;
	  },
	  
	     /* 查看信息*/
	  findschool:function(index){
	  		  this.findstate=true;
	  		  this.bgstate=true;
	  		  this.tempindex=index;
	  		  this.schoolmsg=this.tableData[index];
	  },
	  closefind:function(){
	  		 this.findstate=false;
	  		 this.bgstate=false;
	  		 this.schoolmsg={"id":"0","region":"","schoolname":"","updatetime":"" };
	  },
	 /* 删除信息*/
	  deleteschool:function(){
	  	this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
	        confirmButtonText: '确定',
	        cancelButtonText: '取消',
	        type: 'warning'
	      }).then(() => {
	  		 this.tableData.splice(this.tempindex,1);
	  		this.findstate=false;
	  		this.bgstate=false;
	  		this.tempindex=-1;
	        this.$message({
	  		showClose: true,
	          type: 'success',
	          message: '删除成功!'
	        });
	      }).catch(() => {
	        this.$message({
	  		showClose: true,
	          type: 'info',
	          message: '已取消删除'
	        });          
	      });
	  },
	   /* 删除*/
	  delschool:function(index){
	  		  this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
	      confirmButtonText: '确定',
	      cancelButtonText: '取消',
	      type: 'warning'
	    }).then(() => {
	  			this.tableData.splice(index,1);
	      this.$message({
	  			showClose: true,
	        type: 'success',
	        message: '删除成功!'
	      });
	    }).catch(() => {
	      this.$message({
	  			showClose: true,
	        type: 'info',
	        message: '已取消删除'
	      });          
	    });
	  },
	  /* 编辑*/
	  /* 修改信息*/
	  editschool:function(index){
	  			this.tempData=this.deepCopy(this.tableData[index]);
	  			this.tempindex=index;
	  			this.addstate=true;
	  			this.bgstate=true;
	  			/* 保存数据 */
	  			this.schoolmsg = this.tableData[index];
	  },
	  /* 取消修改*/
	  canceledit:function(){
	  	  this.tableData.splice(this.tempindex,1,this.tempData);
	  	  this.addstate=false;
	  	  this.bgstate=false;
	  	  this.tempData={};
	  	  this.tempindex=-1;
	  },
	   /* TODO重置后不再非空验证*/
	  submitForm(formName) {
	    this.$refs[formName].validate((valid) => {
	  			  if (valid) {
	  				  this.$confirm('此操作将修改该数据, 是否继续?', '提示', {
	  				    confirmButtonText: '确定',
	  				    cancelButtonText: '取消',
	  				    type: 'warning'
	  				  }).then(() => {
	  				  	this.addstate=false;
	  				  	this.bgstate=false;
	  				  	this.schoolmsg={"id":"0","region":"","schoolname":"","updatetime":"" };
	  				    this.$message({
	  				  	showClose: true,
	  				      type: 'success',
	  				      message: '修改成功!'
	  				    });
	  				  }).catch(() => {
	  				    this.$message({
	  				  	showClose: true,
	  				      type: 'info',
	  				      message: '已取消修改'
	  				    });          
	  				  });
	  			  } else {
	  			    console.log('error 修改失败!!');
	  			    return false;
	  			  }
	    });
	  },
	  resetForm(formName) {
	    this.$refs[formName].resetFields();
	  },
  },
	 created:function(){
		  this.$http.get("../../static/schoolintroduction.json").then(
				function(res){
					console.log(res.data);
					this.tableData=res.data;
				},
				function(err){
					console.log(err);
				}
		  ),
		   this.sesname = window.sessionStorage.getItem("username");
		  if(!this.sesname){
		     this.$router.push("/index");
		  }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.schoolrechead{
		background:#F2F2F2;
		height:65px;
		margin-top:20px;
	}
	.schoolrecinput{
		width:180px;
		height:30px;
	}
	.schoolreccoat{
		height:30px;
		line-height: 65px;
		margin-left:20px;
	}
	.el-table{
		margin-top:30px;
		border-top:0;
	}
	
	
	
	/* 新增 */
	.schoolbg{
		width:100%;
		height:100%;
		background: rgba(0,0,0,0.2);
		position:fixed;
		top:0;
		left:0;
		z-index:10
	}
	.schooladdcoat{
		width:50%;
		height:85%;
		position:fixed;
		top:5%;
		left:25%;
		z-index:11;
		background:white;
		padding-left:20px;
		padding-top:22px;
	}
	.schooladdcoat .el-input{
		width:90%;
	}
	.schooladdcoat p{
		padding-bottom:20px;
	}
	.schooladdcoat i{
		float:right;
		margin-right:20px;
	}
	.schooladdcoat .el-button{
		float:right;
		margin-right:25px;
	}
	
	/* 查看模态框 */
	.find{
		width:40%;
		height:100%;
		background:white;
		position:fixed;
		left:30%;
		top:0%;
		z-index:11;
		font-size:13px;
	}
	.find label{
		display:inline-block;
		width:120px;
		text-align:right;
		margin-right:20px;
		margin-left:20px;
	}
	.find ul{
		margin-top:20px;
	}
	.find h5{
		margin-top:5px;
		text-indent:20px;
	}
	.find h5 i{
		float:right;
		margin-right:20px;
	}
	.find li{
		margin-top:10px;
	}
	.find li .el-button{
		margin-left:80px;
	}
	.find .schoolfindbtn{
		margin-left:150px;
	}
</style>



