<template>
  <div id="app">
		<!-- 首页 -->
		<div class="index" v-show="indexstate" >
		  <el-container>
			  <el-header>
				  <img src="../static/logo.png" class="logo" />
				  <ul>
					  <li>欢迎您，蝎子leilei</li>
					  <li @click="changepwd">修改密码</li>
					  <li @click="logout" >退出</li>
				  </ul>
			  </el-header>
			 <el-container>
				 <!-- 左边下拉菜单-->
		  <el-aside width="260px">
			<el-row class="tac">
			<el-col :span="24" class="leftmenu">
		  <el-menu
		    class="el-menu-vertical-demo"
		    @open="handleOpen"
		    @close="handleClose" unique-opened >  <!-- default-active="2" 默认选中 数字对应index-->
			   <routerLink to="/index"><el-menu-item index="5">
		      <i class="el-icon-menu"></i>
		      <span slot="title">首页</span>
		    </el-menu-item></routerLink>			<!-- 首页标签 -->
		    <el-submenu index="1">
		      <template slot="title">
		        <i class="el-icon-date"></i>
		        <span>用户管理</span>
		      </template>
				<routerLink to="/school"><el-menu-item index="1-1">
				  <template slot="title">学校列表</template>
				</el-menu-item></routerLink>
				<routerLink to="/schoolrec"><el-menu-item index="1-2">
				  <template slot="title">学校介绍审核</template>
				</el-menu-item></routerLink>
				<routerLink to="/students"><el-menu-item index="1-3">
				  <template slot="title">学生列表</template>
				</el-menu-item></routerLink>
		      <routerLink to="/teacher"><el-menu-item index="1-4">
		        <template slot="title">教师列表</template>
		      </el-menu-item></routerLink>
				<routerLink to="/parents"><el-menu-item index="1-5">
				  <template slot="title">家长列表</template>
				</el-menu-item></routerLink>
		    </el-submenu>
			  <el-submenu index="2">
			    <template slot="title">
			      <i class="el-icon-picture-outline"></i>
			      <span>信息管理</span>
			    </template>
			    <routerLink to="/news"><el-menu-item index="2-1">
			      <template slot="title">资讯列表</template>
			    </el-menu-item></routerLink>
			    <routerLink to="/beginpage"><el-menu-item index="2-2">
			      <template slot="title">启动页管理</template>
			    </el-menu-item></routerLink>
			    <routerLink to="/ads"><el-menu-item index="2-3">
			      <template slot="title">广告列表</template>
			    </el-menu-item></routerLink>
			  </el-submenu>
			  <el-submenu index="3">
			    <template slot="title">
			      <i class="el-icon-message"></i>
			      <span>意见反馈</span>
			    </template>
			    <routerLink to="/advise"><el-menu-item index="3-1">
			      <template slot="title">意见列表</template>
			    </el-menu-item></routerLink>
			  </el-submenu>
			  <el-submenu index="4">
			    <template slot="title">
			      <i class="el-icon-setting"></i>
			      <span>管理员管理</span>
			    </template>
			    <el-menu-item index="4-1">
			      <template slot="title">管理员列表</template>
			    </el-menu-item>
			    <el-menu-item index="4-2">
			      <template slot="title">权限管理</template>
			    </el-menu-item>
			  </el-submenu>
		  </el-menu>
		</el-col>
			</el-row>
			</el-aside>
			<!-- 右边 组件工厂 -->
		  <el-main>
						<RouterView></RouterView>
			</el-main>
			</el-container>
			</el-container>
			
			</div>
		<!-- 登录框 -->
		<div class="login" v-show="!indexstate">
			<div class="logincoat" >
				<h1 class="loginhead" >系统登录</h1>
				<el-input type="text" class="username" v-model="user" @focus="userby" @blur="userfun" :class="{'loginerr':userbol,'loginok':userok}"></el-input>
				<div class="usererr"  >{{usererr}}</div>
				<el-input type="text" class="password" v-model="pass" @focus="pwdby" @blur="passfun" :class="{'loginerr':pwdbol,'loginok':pwdok}"></el-input>
				<div class="pwderr">{{pwderr}}</div>
				<el-button type="primary" class="loginbtn" :disabled="btnstate" @click="login" >登录</el-button>
			</div>
		</div>
		<!-- 修改密码 -->
		<div class="changepwdbg" v-show="changepwdbol"></div>
		<div class="changepwd" v-show="changepwdbol">
			<p><label>提示</label><i class="el-icon-close" @click="closechange('ruleForm')"></i></p>
				<div class="changepwdcoat">
					<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
						<el-form-item label="原密码" prop="age">
						  <el-input v-model.number="ruleForm.age" clearable></el-input>
						</el-form-item>
					  <el-form-item label="新密码" prop="pass">
					    <el-input type="password" v-model="ruleForm.pass" clearable autocomplete="off"></el-input>
					  </el-form-item>
					  <el-form-item label="确认密码" prop="checkPass">
					    <el-input type="password" v-model="ruleForm.checkPass" clearable autocomplete="off"></el-input>
					  </el-form-item>
					  <el-form-item>
					    <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
					    <el-button @click="resetForm('ruleForm')">重置</el-button>
					  </el-form-item>
					</el-form>
				</div>
		</div>
  </div>
</template>

<script>
import Index from './components/Index';
import School from './components/School';
import SchoolRec from './components/SchoolRec';
import Students from './components/Students';
import Teacher from './components/Teacher';
import Parents from './components/Parents';
import News from './components/News';
import Ads from './components/Ads';
import Advise from './components/Advise';
import Beginpage from './components/Beginpage';
export default {
  name: 'App',
	/* 刷新页面step1 */
	provide(){
		return{
			reload:this.reload
		}
	},
	
	
	data () {
		
		/* 修改密码验证 */
		 var checkAge = (rule, value, callback) => {
				if (!value) {
				  return callback(new Error('密码不能为空'));
				}
				   if (value !== this.password) {
				    callback(new Error('密码错误'));
				  } else {
				    callback();
				  }
		};
		var validatePass = (rule, value, callback) => {
		  if (value === '') {
		    callback(new Error('请输入新密码'));
		  } else if(value===this.password){
				callback(new Error('新密码不能与原密码一致'));
			}else {
		    if (this.ruleForm.checkPass !== '') {
		      this.$refs.ruleForm.validateField('checkPass');
		    }
		    callback();
		  }
		};
		var validatePass2 = (rule, value, callback) => {
		  if (value === '') {
		    callback(new Error('请再次输入密码'));
		  } else if (value !== this.ruleForm.pass) {
		    callback(new Error('两次输入密码不一致!'));
		  } else {
		    callback();
		  }
		};
	  return {
				/* 组建工厂数据 */
			
			/* 刷新页面step2 */
			isRouterAlive:true,
				
			
			
			/* 修改密码模态框 */
			changepwdbol:false,
			user:"",
			pass:"",
			// 提示错误的
			usererr:"",
			pwderr:"",
			// 输入框变红的
			userbol:false,
			pwdbol:false,
			// 接收json当中的
			username:"",
			password:"",
			/* 输入框状态码 */
			userstate:0,
			pwdstate:0,
			/* 按钮禁用 */
			btnstate:true,
			/* 输入合规 */
			userok:false,
			pwdok:false,
			/* 整体显示状态码 */
			indexstate:false,
			/* 储存临时存储信息 */
			sesname:"",
			ruleForm: {
			    pass: '',
			    checkPass: '',
			    age: ''
			  },
				currentPage4: 1	,
			  rules: {
			    pass: [
			      { validator: validatePass, trigger: 'blur' }
			    ],
			    checkPass: [
			      { validator: validatePass2, trigger: 'blur' }
			    ],
			    age: [
			      { validator: checkAge, trigger: 'blur' }
			    ]
			  },
			
			};
	},
  components: {
		Index,
		School,
		SchoolRec,
		Students,
		Teacher,
		Parents,
		News,
		Ads,
		Advise,
		Beginpage
  },
	 methods: {
		 /* 页面刷新step3*/
		 reload(){
			 this.isRouterAlive=false;
			 this.$nextTick(function(){
				 this.isRouterAlive=true;
			 })
		 },
		 
		 /* 修改密码*/
		 changepwd:function(){
			 this.changepwdbol=true;
		 },
		 closechange:function(formName){
			 this.$refs[formName].resetFields();
			 this.changepwdbol=false;
		 },
		 /* 下拉菜单*/
	   handleOpen(key, keyPath) {
	     console.log(key, keyPath);
	   },
	   handleClose(key, keyPath) {
	     console.log(key, keyPath);
	   },
		 /* 聚焦清除错误信息*/
		 userby:function(){
		 	   this.userbol=false;
		 	   this.usererr="";
		   },
		 pwdby:function(){
		   	this.pwdbol=false;
		   	this.pwderr="";
		 },
		 /* 用户名、密码验证*/
		 userfun:function(){
		 	  if(this.user==""){
		 		  this.userbol=true;
		 		  this.usererr="*账号不能为空";
		 		  this.userok=false;
		 		  this.btnstate=true
					this.userstate=0;
		 		  return false;
		 	  };
		 	  if(this.user.length<6 || this.user.length>12){
		 	  	this.userbol=true;
		 	  	this.usererr="*账号长度6-12";
					this.userok=false;
		 	  	this.btnstate=true
					this.userstate=0;
		 	  	return false;
		 	  };
		 	  this.userbol=false;
		 	  this.userok=true;
		 	  this.usererr="";
				this.userstate=1;
				if(this.userstate===1 && this.pwdstate===1){
						this.btnstate=false;
				}
		 	  return true;
		 },
		 passfun:function(){
		   	 if(this.pass==""){
		   		this.pwdbol=true;
		   		this.pwderr="*密码不能为空";
					this.pwdok=false;
		   		this.btnstate=true
					this.pwdstate=0;
		   		return false;
		   	};
		   	if(this.pass.length<6 || this.pass.length>12){
		   		 this.pwdbol=true;
		   		 this.pwderr="*密码长度6-12";
					this.pwdok=false;
		   		 this.pwdtate = 0;
		   		 this.btnstate=true
					 this.pwdstate=0;
		   		 return false;
		  };
		 	this.pwdbol=false;
		 	this.pwdok=true;
		 	this.pwderr="";
			this.pwdstate=1;
			if(this.userstate===1 && this.pwdstate===1){
					this.btnstate=false;
			}
		 	return true;
		 },
		 /* 登录验证*/
		 login:function(){
			 if(this.user===this.username && this.pass===this.password){
				 this.indexstate=true;
				 window.sessionStorage.setItem("username",this.username);
				 this.$message({
				    showClose: true,
				    message: '您已成功登录',
				    type: 'success'
				  });
					this.user="";
					this.pass="";
					this.userok=false;
					this.pwdok=false;
					this.$router.push("/index");
			 }else{
				 this.$message({
          showClose: true,
          message: '用户名或密码错误！',
          type: 'error'
        });
			 }
		 },
		 /* 退出*/
		 logout:function(){
			 this.$confirm('确认是否退出登录?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
					 this.indexstate=false;
					window.sessionStorage.removeItem("username");
          this.$message({
						showClose: true,
            type: 'success',
            message: '您已退出登录，请重新登录'
          });
        }).catch(() => {
          this.$message({
						showClose: true,
            type: 'info',
            message: '已取消退出'
          });          
        });
			
		 },
		 /* 修改密码验证*/
		 submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
              this.$confirm('此操作将修改登录密码，确认是否继续?', '提示', {
                 confirmButtonText: '确定',
                 cancelButtonText: '取消',
                 type: 'warning'
               }).then(() => {
								 this.$message({
										showClose: true,
									  type: 'success',
									  message: '密码修改成功!',
									});
								this.changepwdbol=false;
								 this.$refs[formName].resetFields();
               }).catch(() => {
                 this.$message({
              		showClose: true,
                   type: 'info',
                   message: '用户取消了修改'
                 });          
               });
          } else {
						this.$message({
							showClose: true,
						  type: 'error',
						  message: '用户名密码不能为空!',
						});
						 this.$refs[formName].resetFields();
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
	 },
	 created:function(){
		 /* json验证 */
	 	this.$http.get("../../static/login.json").then(
	 	   	function(res){
	 	  			// console.log(res.data.username);
	 				this.username=res.data.username;
	 				this.password=res.data.password;
	 	    },
	 	    function(err){
	 	  			console.log(err)
	 	   	}
	 	  ),
			this.sesname = window.sessionStorage.getItem("username");
			if(this.sesname){
					this.indexstate=true;
					
			}
	 },
	 
}
</script>

<style>
*{
	margin:0;
	padding:0;
	box-sizing: border-box;
}
ul{
	list-style: none;
}
a{
	text-decoration: none;
}
i:hover{
	cursor:pointer;
}
#app {
	
}
/* 头部 */
.el-header{
    background-color: #5589B1;
    color: white;
    text-align: center;
    line-height: 60px;
  }
  .logo{
	  float:left;
	  margin-top:12px;
	  margin-left:10px;
  }
  .el-header ul{
	  display:flex;
	  flex-wrap:wrap;
	  justify-content: space-around;
	  width:25%;
	  float:right;
	  margin-right:10px;
	  font-size:15px;
  }
	.el-header ul li:hover{
		cursor:pointer;
	}
	/* 主体左 */
  .el-aside {
    color: #333;
    text-align: center;
    line-height: 200px;
  }
	.leftmenu{
		  text-align:left;
	}
  /* 主体右*/
  .el-main {
    color: #333;
  }
  body > .el-container {
    margin-bottom: 40px;
  }
	/* 登录框 */
	.login{
		 width:400px;
		 height:300px;
		 position:fixed;
		 top:25%;
		 left:35%;
		 z-index:2;
		 box-shadow: 0px 0px 10px darkgray;
	}
	.logincoat{
		 width:80%;
		 margin:0 auto;
	}
	.loginhead{
		 text-align:center;
		 font-size:20px;
	}
	.loginbtn{
		 width:100%;
	}
	.loginhead,.username{
		 margin-top:30px;
	}
	.password,.loginbtn{
		 margin-top:10px;
	}
	.usererr,.pwderr{
		 width:100%;
		 height:20px;
		 border:solid 1px transparent;
		 line-height: 20px;
		 font-size:15px;
	}
	.loginerr{
		 border: red 1px solid;
		 border-radius: 5px;
	}
	.loginok{
		 border: greenyellow 1px solid;
		 border-radius: 5px;
	}
	.usererr,.pwderr{
		 color:red
	}
	/* 修改密码 */
	.changepwd{
		width:350px;
		height:350px;
		position:fixed;
		top:25%;
		left:35%;
		z-index:100;
		box-shadow: 0px 0px 10px darkgray;
		background: white;
	}
	.changepwdcoat{
		width:80%;
		float:left;
		margin-top:20px;
	}
	.changepwd p{
		color:#606266;
		text-indent: 20px;
		margin-top:20px;
	}
	.changepwd p i{
		float:right;
		margin-right:20px;
	}
	.changepwd i:hover{
		cursor:pointer;
	}
	.changepwdbg{
		width:100%;
		height:100%;
		position:fixed;
		top:0;
		left:0;
		z-index:5;
		background:rgba(0,0,0,0.2);
	}
	
	
	/* 组件化样式 */
	
	
	
</style>



