<template>
  <div class="school">
	<el-breadcrumb separator="/">
			<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item><a href="/">用户管理</a></el-breadcrumb-item>
			<el-breadcrumb-item>学校列表</el-breadcrumb-item>
		</el-breadcrumb>			
		<div class="schoolhead">
			<div class="schoolcoat">
				<el-input class="schoolinput" placeholder="请输入学校名称"  clearable></el-input>
				<el-button type="primary">搜索</el-button>
				<el-button type="primary">导出学校列表</el-button>
				<el-button type="primary" @click="addschool('ruleForm')" >添加学校</el-button> <!-- TODO添加学校-->
			</div>
		</div>			
		<el-table :data="tableData" border style="width: 100%;"  @current-change="idHandleCurrentChange">
		<el-table-column  label="序号"  type="index" width="55"></el-table-column>
		<el-table-column prop="school_name" label="学校名称"  width="77"></el-table-column>
		<el-table-column prop="location" label="所在区域"  width="77"></el-table-column>
		<el-table-column prop="personcharge" label="负责人"  width="70"></el-table-column>
		<el-table-column prop="phone" label="联系方式"  width="77"></el-table-column>
		<el-table-column prop="teachercount" label="教师数量" width="77" ></el-table-column>	
		<el-table-column prop="studentcount" label="学生数量"  width="77"></el-table-column>
		<el-table-column prop="classcount" label="班级数量"  width="77"></el-table-column>
		<el-table-column prop="checktime" label="入驻时间" width="77" ></el-table-column>	
		<el-table-column prop="state" label="状态"  width="55"></el-table-column>
		<el-table-column prop="pay" label="下次缴费" sortable width="110"></el-table-column>
		<el-table-column label="操作" >
			
		<template slot-scope="scope">
			<el-button size="mini" type="info" @click="findschool(scope.$index)">查看</el-button>
			<el-button size="mini" @click="editschool(scope.$index)">编辑</el-button>
			<el-button size="mini" type="danger" @click="delschool(scope.$index)">删除</el-button>	
        </template>
		
		</el-table-column>	
			
		</el-table>
		<div class="schoolfoot">
			<div class="block">
				<el-pagination @size-change="handleSizeChange"
					@current-change="handleCurrentChange"
					:current-page="currentPage4"
					:page-sizes="[10, 20, 30]"
					:page-size="10"
					layout="total, sizes, prev, pager, next, jumper"
					:total="3">
				</el-pagination>
			</div>
		</div>		
		
		<!-- 新增编辑模态框 -->
	<div class="schooladdcoat" v-show="addstate">
		<p><label>新增/编辑</label><i class="el-icon-close" @click="canceledit"></i></p>
		<el-form  :model="schoolmsg" :rules="rules" ref="ruleForm" label-width="110px" class="demo-ruleForm">
			<el-form-item label="学校名称" prop="school_name" >
				<el-input v-model="schoolmsg.school_name"></el-input>
			</el-form-item>
			<el-form-item label="所在区域" prop="location" >
				<el-select v-model="schoolmsg.location" placeholder="请选择">
					<el-option label="西安" value="西安"></el-option>
					<el-option label="西安1" value="西安1"></el-option>
					<el-option label="西安2" value="西安2"></el-option>
					<el-option label="西安3" value="西安3"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="学校负责人"  prop="personcharge" >
				<el-input v-model="schoolmsg.personcharge"></el-input>
			</el-form-item>
			<el-form-item label="学校地址" prop="schooladdress" >
				<el-input v-model="schoolmsg.schooladdress"></el-input>
			</el-form-item>
			<el-form-item label="管理员手机" prop="phone" >
				<el-input v-model="schoolmsg.phone"></el-input>
			</el-form-item>
			<el-form-item label="备注信息" prop="desc">
				<el-input type="textarea" style="width:90%;" v-model="schoolmsg.desc"></el-input>
			</el-form-item>
			<el-form-item label="下次缴费时间" >
				<el-form-item prop="pay">
				<el-date-picker type="date" placeholder="选择日期"  format="yyyy 年 MM 月 dd 日"
      value-format="yyyy-MM-dd" v-model="schoolmsg.pay" style="width:94%;padding-right:20px;"></el-date-picker>
				</el-form-item>
			</el-form-item>
					<div class="schoolbtncoat">
						<el-button type="primary" @click="submitForm('ruleForm')">确定{{addoredit}}</el-button>
						<el-button @click="resetForm('ruleForm')">重置</el-button>
					</div>
			</el-form>
		</div>		
		<div class="schoolbg" v-show="bgstate"></div>
			
			
			<!-- 查看模态框 -->
			<div class="find"  v-show="findstate">
				<h5><p>查看</p><i class="el-icon-close" @click="closefind"></i></h5>
					<ul >
						<li><label>学校名称：</label>{{schoolmsg.school_name}}</li>
						<li><label>所在区域：</label>{{schoolmsg.location}}</li>
						<li><label>学校负责人：</label>{{schoolmsg.personcharge}}</li>
						<li><label>教师数量：</label>{{schoolmsg.teachercount}}<el-button type="primary" size="mini" round @click="linktoteacher" >教师列表</el-button></li>
						<li><label>学生数量：</label>{{schoolmsg.studentcount}}<el-button type="primary" size="mini" round @click="linktostudent">学生列表</el-button></li>
						<li><label>家长数量：</label>{{schoolmsg.parentcount}}<el-button type="primary" size="mini" round @click="linktoparent">家长列表</el-button></li>
						<li><label>班级数量：</label>{{schoolmsg.classcount}}</li>
						<li><label>入住时间：</label>{{schoolmsg.checktime}}</li>
						<li><label>下次缴费时间：</label>{{schoolmsg.pay}}</li>
						<li><label>学校地址：</label>{{schoolmsg.schooladdress}}</li>
						<li><label>备注信息：</label></li>
						<li><label>管理员手机：</label>{{schoolmsg.phone}}<el-button type="primary" size="mini" round >重置密码</el-button></li>
						<li><label style="float:left;">学校logo：</label>
							<img src="../../static/vue.png" />
						</li>
						<div class="schoolfindbtn">
							<el-button type="primary">停用学校</el-button>
							<el-button type="danger" @click="deleteschool">删除学校</el-button>
						</div>
					</ul>
			</div>	
		
  </div>
</template>

<script>
export default {
  name: 'School',
  data () {
    return {
		/* 新增/编辑判断 */
		sesname:"",
		tempData:{},
		tempindex:-1,
		addoredit:"",
		findstate:false,
		addstate:false,
		bgstate:false,
		schoolmsg:{id:0,school_name:"",location:"",personcharge:"",phone:"",teachercount:"50",studentcount:"300",classcount:"10",checktime:"2019-05-18",state:"正常",pay:"",parentcount:130,schooladdress:""},
		tableData: [],
			currentPage4: 1	,
			 ruleForm: {
			location:'',
		    school_name: '',
		    schooladdress: '',
		    personcharge: '',
		    pay: '',
		    schooladdress: '',
		    phone: '',
		  },
		  rules: {
		    school_name: [
		      { required: true, message: '请输入学校名称', trigger: 'change' },
		      { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'change' },
			  {pattern: /^[\u4E00-\u9FA5]+$/, message: '学校名只能为中文'}
		    ],
		    location: [
		      { required: true, message: '请选择活动区域', trigger: 'change' }
		    ],
		    personcharge: [
		      { required: true,  message: '请正确输入人名', trigger: 'change' },
			   {pattern: /^[\u4E00-\u9FA5]+$/, message: '负责人名只能为中文'}
		    ],
		    pay: [
		      { required: true, message: '请选择时间', trigger: 'change' }
		    ],
		    schooladdress: [
		      { required: true, message: '请正确输入地址', trigger: 'change' }
		    ],
		    phone: [
		      { required: true, message: '请正确输入手机号', trigger: 'change' },
			  { pattern: /^1[34578]\d{9}$/, message: '目前只支持中国大陆的手机号码' }
		    ]
		},
    }
  },
  methods:{
	  /* ID改序号*/
	   idHandleCurrentChange(val) {
        this.currentRow = val;
      },
	  /* 深拷贝*/
	  deepCopy:function (obj) {
      var result = Array.isArray(obj) ? [] : {};
      for (var key in obj) {
        if (obj.hasOwnProperty(key)) {
          if (typeof obj[key] === 'object' && obj[key]!==null) {
            result[key] = deepCopy(obj[key]);   //递归复制
          } else {
            result[key] = obj[key];
          }
        }
      }
      return result;
    },
	/* 查看页面删除学校 */
	deleteschool:function(){
		this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
			 this.tableData.splice(this.tempindex,1);
			this.findstate=false;
			this.bgstate=false;
			this.tempindex=-1;
          this.$message({
			showClose: true,
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
			showClose: true,
            type: 'info',
            message: '已取消删除'
          });          
        });
	},
	  formatter(row, column) {
	     return row.address;
	   },
	  handleSizeChange(val) {
	     console.log(`每页 ${val} 条`);
	   },
	   handleCurrentChange(val) {
	     console.log(`当前页: ${val}`);
	   },
	   /* 查看信息*/
	  findschool:function(index){
		  this.findstate=true;
		  this.bgstate=true;
		  this.tempindex=index;
		  this.schoolmsg=this.tableData[index];
	  },
	  closefind:function(){
	  		 this.findstate=false;
	  		 this.bgstate=false;
	  		 this.schoolmsg={id:0,school_name:"",location:"",personcharge:"",phone:"",teachercount:"50",studentcount:"300",classcount:"10",checktime:"2019-05-18",state:"正常",pay:"",parentcount:130,schooladdress:""};
	  },
	  /* 增改信息*/
	  editschool:function(index){
			this.tempData=this.deepCopy(this.tableData[index]);
			this.tempindex=index;
			this.addoredit="修改";
			this.addstate=true;
			this.bgstate=true;
			/* 保存数据 */
			this.schoolmsg = this.tableData[index];
	  },
	  /* 取消新增或修改*/
	  canceledit:function(){
		  if(this.addoredit=="修改"){
			  this.tableData.splice(this.tempindex,1,this.tempData);
			  this.addstate=false;
			  this.bgstate=false;
			  this.tempData={};
			  this.tempindex=-1;
		  }else if(this.addoredit=="新增"){
			  this.addstate=false;
			  this.bgstate=false;
		  }else{
			  console.log("404");
		  }
	  },
	  
	  /* TODO重置后不再非空验证*/
	  submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if(this.addoredit=="修改"){
			  if (valid) {
				  this.$confirm('此操作将修改该数据, 是否继续?', '提示', {
				    confirmButtonText: '确定',
				    cancelButtonText: '取消',
				    type: 'warning'
				  }).then(() => {
				  	this.addstate=false;
				  	this.bgstate=false;
				  	this.schoolmsg={id:0,school_name:"",location:"",personcharge:"",phone:"",teachercount:"50",studentcount:"300",classcount:"10",checktime:"2019-05-18",state:"正常",pay:"",parentcount:130,schooladdress:""};
				    this.$message({
				  	showClose: true,
				      type: 'success',
				      message: '修改成功!'
				    });
				  }).catch(() => {
				    this.$message({
				  	showClose: true,
				      type: 'info',
				      message: '已取消修改'
				    });          
				  });
			  } else {
			    console.log('error 修改失败!!');
			    return false;
			  }
		  }else if(this.addoredit=="新增"){
			  if (valid) {
				  this.$message({
				 showClose: true,
				   type: 'success',
				   message: '新增成功!'
				 });
				this.tableData.unshift(this.schoolmsg);
			  			this.addstate=false;
			  			this.bgstate=false;
						this.schoolmsg={id:0,school_name:"",location:"",personcharge:"",phone:"",teachercount:"50",studentcount:"300",classcount:"10",checktime:"2019-05-18",state:"正常",pay:"",parentcount:130,schooladdress:""};
			  } else {
			    console.log('error 新增失败!!');
			    return false;
			  }
		  }
        });
      },
	  resetForm(formName) {
        this.$refs[formName].resetFields();
      },
	  /* 删除*/
	  delschool:function(index){
		  this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
			this.tableData.splice(index,1);
          this.$message({
			showClose: true,
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
			showClose: true,
            type: 'info',
            message: '已取消删除'
          });          
        });
	  },
	  /* 新增*/
	  addschool:function(formName){
		  this.$refs[formName].resetFields();
		  this.addoredit="新增";
		  this.addstate=true;
		  this.bgstate=true;
	  },
	 linktoteacher:function(){
		 this.$router.push("/teacher");
	 },
	 linktostudent:function(){
	 	this.$router.push("/students");
	 },
	 linktoparent:function(){
	 	this.$router.push("/parents");
	 },
  },
  created:function(){
	  this.$http.get("../../static/schoollist.json").then(
			function(res){
				this.tableData=res.data;
			},
			function(err){
				console.log(err);
			}
	  ),
	  this.sesname = window.sessionStorage.getItem("username");
	  if(!this.sesname){
	     this.$router.push("/index");
	  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.schoolhead{
		background:#F2F2F2;
		height:65px;
		margin-top:20px;
	}
	.schoolinput{
		width:180px;
		height:30px;
	}
	.schoolcoat{
		height:30px;
		line-height: 65px;
		margin-left:20px;
	}
	.el-table{
		margin-top:30px;
		border-top:0;
	}
	/* 新增 */
	.schoolbg{
		width:100%;
		height:100%;
		background: rgba(0,0,0,0.2);
		position:fixed;
		top:0;
		left:0;
		z-index:10
	}
	.schooladdcoat{
		width:50%;
		height:85%;
		position:fixed;
		top:5%;
		left:25%;
		z-index:11;
		background:white;
		padding-left:20px;
		padding-top:22px;
	}
	.schooladdcoat .el-input{
		width:90%;
	}
	.schooladdcoat p{
		padding-bottom:20px;
	}
	.schooladdcoat i{
		float:right;
		margin-right:20px;
	}
	.schooladdcoat .el-button{
		float:right;
		margin-right:25px;
	}
	
	/* 查看模态框 */
	.find{
		width:40%;
		height:100%;
		background:white;
		position:fixed;
		left:30%;
		top:0%;
		z-index:11;
		font-size:13px;
	}
	.find label{
		display:inline-block;
		width:120px;
		text-align:right;
		margin-right:20px;
		margin-left:20px;
	}
	.find ul{
		margin-top:20px;
	}
	.find h5{
		margin-top:5px;
		text-indent:20px;
	}
	.find h5 i{
		float:right;
		margin-right:20px;
	}
	.find li{
		margin-top:10px;
	}
	.find li .el-button{
		margin-left:80px;
	}
	.find .schoolfindbtn{
		margin-left:150px;
	}
</style>




