<template>
  <div class="parents">
		<!-- 头部 -->
		<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item><a href="/">用户管理</a></el-breadcrumb-item>
				<el-breadcrumb-item>家长列表</el-breadcrumb-item>
		</el-breadcrumb>	
		<!-- 搜索 -->
		<div class="parentshead">
			<div class="parentscoat">
				<el-select v-model="value" clearable placeholder="请选择学校">
			<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
			</el-option>
				</el-select>
					<el-input class="parentsinput" size="medium" placeholder="请输入学生姓名"  clearable></el-input>
					<el-input class="parentsinput" size="medium" placeholder="请输入学籍号"  clearable></el-input>
				<el-button type="primary">搜索</el-button>
			</div>
		</div>
		<!-- 表格 -->
		 <el-table :data="tableData" border style="width: 100%;" :default-sort = "{prop: 'id', order: 'descending'}">
		<el-table-column prop="id" label="序号"  width="65"></el-table-column>
		<el-table-column prop="parentname" label="家长姓名" width="200" ></el-table-column>	
		<el-table-column prop="phone" label="家长手机"  width="200"></el-table-column>
		<el-table-column prop="studentname" label="学生姓名" width="200"></el-table-column>
		<el-table-column prop="studentnum" label="学生学籍号"  width="200"></el-table-column>
		<el-table-column prop="school" label="所属学校"  width="200"></el-table-column>
		</el-table>
		
		<!-- 分页 -->
		<div class="parentsfoot">
			<div class="block">
				<el-pagination @size-change="handleSizeChange"
					@current-change="handleCurrentChange"
					:current-page="currentPage4"
					:page-sizes="[10, 20, 30]"
					:page-size="10"
					layout="total, sizes, prev, pager, next, jumper"
					:total="3">
				</el-pagination>
			</div>
		</div>	
  </div>
</template>

<script>
export default {
  name: 'Parents',
  data () {
    return {
		tableData:[],
		options: [{
		    value: '选项1',
		    label: '黄金糕'
		  }, {
		    value: '选项2',
		    label: '双皮奶'
		  }, {
		    value: '选项3',
		    label: '蚵仔煎'
		  }, {
		    value: '选项4',
		    label: '龙须面'
		  }, {
		    value: '选项5',
		    label: '北京烤鸭'
		  }],
		  value: '',
		  currentPage4: 1,
		  sesname:''
    }
  },
  methods:{
	  handleSizeChange(val) {
	     console.log(`每页 ${val} 条`);
	   },
	   handleCurrentChange(val) {
	     console.log(`当前页: ${val}`);
	   },
  },
  created:function(){
  	  this.$http.get("../../static/parentlist.json").then(
  			function(res){
  				console.log(res.data);
  				this.tableData=res.data;
  			},
  			function(err){
  				console.log(err);
  			}
  	  ),
	   this.sesname = window.sessionStorage.getItem("username");
	  if(!this.sesname){
	     this.$router.push("/index");
	  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.parentshead{
		background:#F2F2F2;
		height:65px;
		margin-top:20px;
	}
	.parentsinput{
		width:150px;
		height:30px;
	}
	.parentscoat{
		height:30px;
		line-height: 65px;
		margin-left:20px;
	}
	.el-table{
		margin-top:30px;
		border-top:0;
	}
</style>



