<template>
  <div class="beginpage">
    <!-- 头部 -->
    <el-breadcrumb separator="/">
    		<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
    		<el-breadcrumb-item><a href="/">信息管理</a></el-breadcrumb-item>
    		<el-breadcrumb-item>启动页管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-main class="beginpageleft">
    	<ul>
    		<h1>家长端APP<el-button size="mini" type="primary">提交</el-button></h1>
    		<!-- 封面 -->
    		<el-form  :model="ruleForm" :rules="rules" ref="ruleForm" label-width="70px" class="demo-ruleForm">
    		<el-form-item label="封面">
    				<el-upload
    					class="avatar-uploader123"
    					action="https://jsonplaceholder.typicode.com/posts/"
    					:show-file-list="false"
    					:on-success="handleAvatarSuccess"
    					:before-upload="beforeAvatarUpload">
    					<img v-if="imageUrl" :src="imageUrl" class="avatar">
    					<i v-else class="el-icon-plus avatar-uploader-icon"></i>
    				</el-upload>
    		</el-form-item>
    		<el-form-item label="外链" prop="name">
    			<el-input v-model="ruleForm.name"></el-input>
    		</el-form-item>
    		</el-form>
    	</ul>
    </el-main>
    <el-main class="beginpageright">
    		<ul>
    		<h1>教师端APP<el-button size="mini" type="primary">提交</el-button></h1>
    		<!-- 封面 -->
    	<el-form  :model="ruleForm" :rules="rules" ref="ruleForm" label-width="70px" class="demo-ruleForm">
    		<el-form-item label="封面">
    				<el-upload
    					class="avatar-uploader123"
    					action="https://jsonplaceholder.typicode.com/posts/"
    					:show-file-list="false"
    					:on-success="handleAvatarSuccess"
    					:before-upload="beforeAvatarUpload">
    					<img v-if="imageUrl" :src="imageUrl" class="avatar">
    					<i v-else class="el-icon-plus avatar-uploader-icon"></i>
    				</el-upload>
    		</el-form-item>
    		<el-form-item label="外链" prop="name">
    			<el-input v-model="ruleForm.name"></el-input>
    		</el-form-item>
    		</el-form>
    	</ul>
    </el-main>
  </div>
</template>

<script>
export default {
  name: 'Beginpage',
  data () {
    return {
		imageUrl: '',
			ruleForm: {
			  name: '',
			  region: '',
			  person: '',
			  date2: '',
			  'add': '',
			  phone: '',
			  desc: ''
			},
			
				rules: {
			     name: [
			       { required: true, message: '请输入url', trigger: 'change' },
			     ]
			},
    }
  },
	methods:{
		handleAvatarSuccess(res, file) {
		    this.imageUrl = URL.createObjectURL(file.raw);
		  },
		  beforeAvatarUpload(file) {
		    const isJPG = file.type === 'image/jpeg';
		    const isLt2M = file.size / 1024 / 1024 < 2;
		
		    if (!isJPG) {
		      this.$message.error('上传头像图片只能是 JPG 格式!');
		    }
		    if (!isLt2M) {
		      this.$message.error('上传头像图片大小不能超过 2MB!');
		    }
		    return isJPG && isLt2M;
		  },
	},
	created:function(){
		 this.sesname = window.sessionStorage.getItem("username");
		if(!this.sesname){
		   this.$router.push("/index");
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.beginpageleft{
			float:left;
			width:600px;
	  }
	  .beginpageleft ul{
		  width: 445px;
		  height:350px;
		  box-shadow: 0px 0px 5px darkgray;
	  }
	  .beginpageleft ul h1{
		  height:55px;
		  line-height:55px;
		  border-bottom:solid 1px #EFF1F7;
		  text-indent: 20px;
		  margin-bottom:10px;
			font-size:15px;
	  }
	  .beginpageleft ul li{
		  height:35px;
		  line-height:35px;
		  width:405px;
		  margin:0 auto;
		  border-bottom:solid 1px #EFF1F7;
		  font-size:15px;
	  }
	
	
	.beginpageright{
			float:left;
			width:600px;
	  }
	  .beginpageright ul{
		  width: 445px;
		  height:350px;
		  box-shadow: 0px 0px 5px darkgray;
	  }
	  .beginpageright ul h1{
		  height:55px;
		  line-height:55px;
		  border-bottom:solid 1px #EFF1F7;
		  text-indent: 20px;
		  margin-bottom:10px;
			font-size:15px;
	  }
	  .beginpageright ul li{
		  height:35px;
		  line-height:35px;
		  width:405px;
		  margin:0 auto;
		  border-bottom:solid 1px #EFF1F7;
		  font-size:15px;
	  }
		.beginpageright .el-input{
			width:90%;
		}
		.beginpageleft .el-input{
			width:90%;
		}
		.beginpageright .el-button{
			float:right;
			margin-right:20px;
			margin-top:15px;
		}
		.beginpageleft .el-button{
			float:right;
			margin-right:20px;
			margin-top:15px;
		}
	.avatar-uploader123{
	  border: 1px dashed #d9d9d9;
	  width:178px;
	  border-radius: 6px;
	  cursor: pointer;
	  position: relative;
	  overflow: hidden;
	}
	.avatar-uploader123:hover {
	  border-color: #409EFF;
	}
	.avatar-uploader-icon {
	  font-size: 28px;
	  color: #8c939d;
	  width: 178px;
	  height: 178px;
	  line-height: 178px;
	  text-align: center;
	}
	.avatar {
	  width: 178px;
	  height: 178px;
	  display: block;
	}
</style>



