<template>
  <div class="advise">
    <!-- 头部 -->
    <el-breadcrumb separator="/">
    		<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
    		<el-breadcrumb-item><a href="/">意见反馈</a></el-breadcrumb-item>
    		<el-breadcrumb-item>意见列表</el-breadcrumb-item>
    </el-breadcrumb>	
    <!-- 搜索 -->
    <div class="advisehead">
    	<div class="advisecoat">
    		<el-select v-model="value" clearable placeholder="请选择反馈类型">
    	<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
    	</el-option>
    		</el-select>
    		<el-button type="primary">搜索</el-button>
    	</div>
    </div>
    <!-- 表格 -->
    <el-table :data="tableData" border style="width: 100%;" @current-change="idHandleCurrentChange" >
    <el-table-column type="index" label="序号"  width="65"></el-table-column>
    <el-table-column prop="feedbackusername" label="反馈账号" width="115" ></el-table-column>	
    <el-table-column prop="feedbackname" label="反馈人"  width="115"></el-table-column>
    <el-table-column prop="identity" label="身份" width="115"></el-table-column>
    <el-table-column prop="idea" label="意见"  width="115"></el-table-column>
    <el-table-column prop="type" label="类型"  width="115"></el-table-column>
    <el-table-column prop="ideatime" label="时间" width="125"></el-table-column>
    <el-table-column prop="" label="操作"  >
	    <template slot-scope="scope">
	    	<el-button size="mini" type="info" @click="findschool(scope.$index)">查看</el-button>
	    	<el-button size="mini" @click="editschool(scope.$index)">编辑</el-button>
	    	<el-button size="mini" type="danger" @click="delschool(scope.$index)">删除</el-button>	
	    </template>
    </el-table-column>
    </el-table>
    
    <!-- 分页 -->
    <div class="advisefoot">
    	<div class="block">
    		<el-pagination @size-change="handleSizeChange"
    			@current-change="handleCurrentChange"
    			:current-page="currentPage4"
    			:page-sizes="[10, 20, 30]"
    			:page-size="10"
    			layout="total, sizes, prev, pager, next, jumper"
    			:total="3">
    		</el-pagination>
    	</div>
    </div>	
    
    <!-- 模态框？？？ -->
    
    <div class="schoolbg" v-show="bgstate"></div>
    	
    	
    	<!-- 查看模态框 -->
    	<div class="find"  v-show="findstate">
    		<h5><p>查看</p><i class="el-icon-close" @click="closefind"></i></h5>
    			<ul >
    				<li><label>反馈账号：</label>{{schoolmsg.feedbackusername}}</li>
    				<li><label>反馈人：</label>{{schoolmsg.feedbackname}}</li>
    				<li><label>身份：</label>{{schoolmsg.identity}}</li>
				<li><label>意见：</label>{{schoolmsg.idea}}</li>
				<li><label>类型：</label>{{schoolmsg.type}}</li>
				<li><label>时间：</label>{{schoolmsg.ideatime}}</li>
    				<li><label style="float:left;">学校logo：</label>
    					<img src="../../static/vue.png" />
    				</li>
    				<div class="schoolfindbtn">
    					<el-button type="primary">停用意见</el-button>
    					<el-button type="danger" @click="deleteschool">删除意见</el-button>
    				</div>
    			</ul>
    	</div>	
    
    <!-- 新增编辑模态框 -->
    <div class="schooladdcoat" v-show="addstate">
    	<p><label>编辑</label><i class="el-icon-close" @click="canceledit"></i></p>
    	<el-form  :model="schoolmsg" :rules="rules" ref="ruleForm" label-width="110px" class="demo-ruleForm">
    		<el-form-item label="反馈人" prop="feedbackname" >
    			<el-input v-model="schoolmsg.feedbackname"></el-input>
    		</el-form-item>
		<el-form-item label="反馈账号" prop="feedbackusername" >
			<el-input v-model="schoolmsg.feedbackusername"></el-input>
		</el-form-item>
		<el-form-item label="身份" prop="identity" >
			<el-input v-model="schoolmsg.identity"></el-input>
		</el-form-item>
		<el-form-item label="意见" prop="idea" >
			<el-input v-model="schoolmsg.idea"></el-input>
		</el-form-item>
    		<el-form-item label="反馈类型" prop="type" >
    			<el-select v-model="schoolmsg.type" placeholder="请选择">
    				<el-option label="软件" value="软件"></el-option>
    				<el-option label="硬件" value="硬件"></el-option>
    				<el-option label="教学" value="教学"></el-option>
    			</el-select>
    		</el-form-item>
    		<el-form-item label="反馈时间" >
    			<el-form-item prop="ideatime">
    			<el-date-picker type="date" placeholder="选择日期"  format="yyyy 年 MM 月 dd 日"
      value-format="yyyy-MM-dd" v-model="schoolmsg.ideatime" style="width:94%;padding-right:20px;"></el-date-picker>
    			</el-form-item>
    		</el-form-item>
    				<div class="schoolbtncoat">
    					<el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
    					<el-button @click="resetForm('ruleForm')">重置</el-button>
    				</div>
    		</el-form>
    	</div>		
  </div>
</template>

<script>
export default {
  name: 'Advise',
  data () {
    return {
tableData:[],
		options: [{
	    value: '选项1',
	    label: '硬件'
	  }, {
	    value: '选项2',
	    label: '软件'
	  }, {
	    value: '选项3',
	    label: '教学'
	  }, ],
	  value: '',
	  currentPage4: 1,
	  tempData:{},
	  tempindex:-1,
	  /* 查看模态框 */
	  findstate:false,
	  addstate:false,
	  bgstate:false,
	  sesname:"",
	  schoolmsg:{"id":"111","feedbackusername":"","feedbackname":"","identity":"","idea":"","type":"","ideatime":"2019-02-13"},
	  	 ruleForm: {
	      feedbackname: '',
	      ideatime: '',
	      feedbackusername:'',
	      identity:'',
	      idea:'',
	      type:''
	    },
	    rules: {
	      feedbackname: [
	        { required: true, message: '请输入反馈人', trigger: 'change' },
	        { min: 3, max: 10, message: '长度在 3 到 10个字符', trigger: 'change' }
	      ],
	      type: [
	        { required: true, message: '请输入类型', trigger: 'change' }
	      ],
	      ideatime: [
	        { required: true, message: '请选择时间', trigger: 'change' }
	      ],
	       feedbackusername: [
	        { required: true, message: '请输入账号', trigger: 'change' },
			{pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/, message: '请输入正确的邮箱格式'}
	      ],
	       identity: [
	        { required: true, message: '请输入身份', trigger: 'change' }
	      ],
	       idea: [
	        { required: true, message: '请输入意见', trigger: 'change' }
	      ]
	  }
    }
  },
	methods:{
		/* ID改序号*/
		 idHandleCurrentChange(val) {
		  this.currentRow = val;
		},
		handleSizeChange(val) {
		   console.log(`每页 ${val} 条`);
		 },
		 handleCurrentChange(val) {
		   console.log(`当前页: ${val}`);
		 },
		 /* 深拷贝*/
		    deepCopy:function (obj) {
		    var result = Array.isArray(obj) ? [] : {};
		    for (var key in obj) {
		      if (obj.hasOwnProperty(key)) {
		        if (typeof obj[key] === 'object' && obj[key]!==null) {
		          result[key] = deepCopy(obj[key]);   //递归复制
		        } else {
		          result[key] = obj[key];
		        }
		      }
		    }
		    return result;
		  },
		  
		     /* 查看信息*/
		  findschool:function(index){
		  		  this.findstate=true;
		  		  this.bgstate=true;
		  		  this.tempindex=index;
		  		  this.schoolmsg=this.tableData[index];
		  },
		  closefind:function(){
		  		 this.findstate=false;
		  		 this.bgstate=false;
		  		 this.schoolmsg={"id":"111","feedbackusername":"","feedbackname":"","identity":"","idea":"","type":"","ideatime":"2019-02-13"};
		  },
		 /* 删除信息*/
		  deleteschool:function(){
		  	this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
		        confirmButtonText: '确定',
		        cancelButtonText: '取消',
		        type: 'warning'
		      }).then(() => {
		  		 this.tableData.splice(this.tempindex,1);
		  		this.findstate=false;
		  		this.bgstate=false;
		  		this.tempindex=-1;
		        this.$message({
		  		showClose: true,
		          type: 'success',
		          message: '删除成功!'
		        });
		      }).catch(() => {
		        this.$message({
		  		showClose: true,
		          type: 'info',
		          message: '已取消删除'
		        });          
		      });
		  },
		   /* 删除*/
		  delschool:function(index){
		  		  this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
		      confirmButtonText: '确定',
		      cancelButtonText: '取消',
		      type: 'warning'
		    }).then(() => {
		  			this.tableData.splice(index,1);
		      this.$message({
		  			showClose: true,
		        type: 'success',
		        message: '删除成功!'
		      });
		    }).catch(() => {
		      this.$message({
		  			showClose: true,
		        type: 'info',
		        message: '已取消删除'
		      });          
		    });
		  },
		  /* 编辑*/
		  /* 修改信息*/
		  editschool:function(index){
		  			this.tempData=this.deepCopy(this.tableData[index]);
		  			this.tempindex=index;
		  			this.addstate=true;
		  			this.bgstate=true;
		  			/* 保存数据 */
		  			this.schoolmsg = this.tableData[index];
		  },
		  /* 取消修改*/
		  canceledit:function(){
		  	  this.tableData.splice(this.tempindex,1,this.tempData);
		  	  this.addstate=false;
		  	  this.bgstate=false;
		  	  this.tempData={};
		  	  this.tempindex=-1;
		  },
		   /* TODO重置后不再非空验证*/
		  submitForm(formName) {
		    this.$refs[formName].validate((valid) => {
  if (valid) {
	  this.$confirm('此操作将修改该数据, 是否继续?', '提示', {
	    confirmButtonText: '确定',
	    cancelButtonText: '取消',
	    type: 'warning'
	  }).then(() => {
	  	this.addstate=false;
	  	this.bgstate=false;
	  	this.schoolmsg={"id":"111","feedbackusername":"","feedbackname":"","identity":"","idea":"","type":"","ideatime":"2019-02-13"};
	    this.$message({
	  	showClose: true,
	      type: 'success',
	      message: '修改成功!'
	    });
	  }).catch(() => {
	    this.$message({
	  	showClose: true,
	      type: 'info',
	      message: '已取消修改'
	    });          
	  });
  } else {
    console.log('error 修改失败!!');
    return false;
  }
		    });
		  },
		  resetForm(formName) {
		    this.$refs[formName].resetFields();
		  },
	},
	created:function(){
		  this.$http.get("../../static/idealist.json").then(
				function(res){
					console.log(res.data);
					this.tableData=res.data;
				},
				function(err){
					console.log(err);
				}
		  ),
		  this.sesname = window.sessionStorage.getItem("username");
		  if(!this.sesname){
		     this.$router.push("/index");
		  }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.advisehead{
		background:#F2F2F2;
		height:65px;
		margin-top:20px;
	}
	.adviseinput{
		width:150px;
		height:30px;
	}
	.advisecoat{
		height:30px;
		line-height: 65px;
		margin-left:20px;
	}
	.el-table{
		margin-top:30px;
		border-top:0;
	}
/* 新增 */
.schoolbg{
width:100%;
height:100%;
background: rgba(0,0,0,0.2);
position:fixed;
top:0;
left:0;
z-index:10
}
.schooladdcoat{
	width:50%;
	height:85%;
	position:fixed;
	top:5%;
	left:25%;
	z-index:11;
	background:white;
	padding-left:20px;
	padding-top:22px;
}
.schooladdcoat .el-input{
	width:90%;
}
.schooladdcoat p{
	padding-bottom:20px;
}
.schooladdcoat i{
	float:right;
	margin-right:20px;
}
.schooladdcoat .el-button{
	float:right;
	margin-right:25px;
}

/* 查看模态框 */
.find{
	width:40%;
	height:100%;
	background:white;
	position:fixed;
	left:30%;
	top:0%;
	z-index:11;
	font-size:13px;
}
.find label{
	display:inline-block;
	width:120px;
	text-align:right;
	margin-right:20px;
	margin-left:20px;
}
.find ul{
	margin-top:20px;
}
.find h5{
	margin-top:5px;
	text-indent:20px;
}
.find h5 i{
	float:right;
	margin-right:20px;
}
.find li{
	margin-top:10px;
}
.find li .el-button{
	margin-left:80px;
}
.find .schoolfindbtn{
	margin-left:150px;
}
</style>



