// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import Echarts from 'echarts';
import App from './App';
import VueResource from 'vue-resource';
import VueRouter from 'vue-router';
import Index from './components/Index';
import School from './components/School';
import SchoolRec from './components/SchoolRec';
import Students from './components/Students';
import Teacher from './components/Teacher';
import Parents from './components/Parents';
import Ads from './components/Ads';
import Advise from './components/Advise';
import News from './components/News';
import Beginpage from './components/Beginpage';


Vue.config.productionTip = false;
Vue.use(ElementUI);
Vue.use(VueResource);
Vue.use(VueRouter);
Vue.use(Echarts);
Vue.prototype.$echarts = Echarts;
/* eslint-disable no-new */
const router = new VueRouter({
		routes:[
			// {path:"",component:Index},
			{path:"/index",component:Index},
			{path:"/school",component:School},
			{path:"/schoolrec",component:SchoolRec},
			{path:"/students",component:Students},
			{path:"/teacher",component:Teacher},
			{path:"/parents",component:Parents},
			{path:"/ads",component:Ads},
			{path:"/news",component:News},
			{path:"/advise",component:Advise},
			{path:"/beginpage",component:Beginpage},
		],
		mode:"history"
})
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
	router
})



